          if (errorCode === "42P01") {
            console.warn("Profiles ÌÖåÏù¥Î∏îÏù¥ ÏóÜÏäµÎãàÎã§. ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖòÏùÑ Ïã§ÌñâÌï¥Ï£ºÏÑ∏Ïöî.");
            toast.error("ÌîÑÎ°úÌïÑ ÌÖåÏù¥Î∏îÏù¥ ÏóÜÏäµÎãàÎã§. Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌïòÏÑ∏Ïöî.");
          } else if (errorCode === "23505") {
            // Unique constraint violation - nickname Ï§ëÎ≥µ
            toast.error("Ïù¥ÎØ∏ ÏÇ¨Ïö© Ï§ëÏù∏ ÎãâÎÑ§ÏûÑÏûÖÎãàÎã§.");
          } else {
            const errorMessage = (errorInfo.message as string) || "Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò";
            toast.error(`ÌîÑÎ°úÌïÑ ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§: ${errorMessage}`);
          }
          
          // ÌîÑÎ°úÌïÑ ÏÉùÏÑ± Ïã§Ìå®Ìï¥ÎèÑ ÌöåÏõêÍ∞ÄÏûÖÏùÄ ÏôÑÎ£åÎêú ÏÉÅÌÉúÏù¥ÎØÄÎ°ú Í≥ÑÏÜç ÏßÑÌñâ
